  import Loading from '../../public/Loading'
  import Person from "../../public/person.vue"
	export default {
		data() {
			return {
			  api:this.GLOBAL.baseURL,
				activeName:"first",
        replay:'说点什么...',
        overflow:'hidden',
        height:'78px',
        review:{
          text:''
        },
        isLogin:true,
        imgUrl:'',
        isReplay:false,
        otherList:[],
        commenImg:'../../../static/img/static/user.png',
        replayId:'',
        flag:'',
        myConment:[],
        isLoading:false
			}
    },
    components:{
      Person,
      Loading
    },
    methods:{
		  getToMe(val){
		    this.isLoading = true
		    this.$http.post(this.api + '/msgreply',{
		      token: localStorage.getItem('token'),
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.otherList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getToMe(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      abolish(){
        this.review.text = ''
        this.height = '78px'
        this.overflow= 'hidden'
        this.replay = '说点什么...'
      },
      textareaFocus(){
        this.height = 'auto'
        this.overflow = 'none'
      },
      discuss(){
		    if(this.replayId){
          this.$http.post(this.api + '/home/Comment/save_leavemsg',{
            token: localStorage.getItem('token'),
            flag: this.flag == 2||this.flag == 5?5:this.flag == 4?4:3,
            content: this.review.text,
            table_id: this.replayId
          })
            .then(res=>{
              if(res.data.code == 1){
                this.review.text = ''
                this.$message({
                  message:'评论成功',
                  type:'success'
                })
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.discuss()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }

      },
      sendReplay(item){
        document.getElementById('textarea').focus()
        this.replayId = item.say_id
        this.flag = item.flag_comm
        this.replay = '回复给' + item.family_name+item.middle_name+item.name
      },
      getMy(val){
		    this.$http.post(this.api + '/msgsay',{
		      token:localStorage.getItem('token'),
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.myConment = res.data.data.data
            }else if(res.data.code == 3){
              this.$http.post(rhis.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getMy(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      handClick(){
		    if(this.activeName == 'second'){
		      this.getMy(1)
        }else{
          this.getToMe(1)
        }
      },
      //删除
      deleteCo(item,index){
        this.$confirm('此操作将永久删除该评论, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.reduceComment(item,index)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      reduceComment(item,index){
		    if(item.flag == 1||item.flag == 6){
          this.$http.post(this.api + '/home/Comment/del_comment',{
            token:localStorage.getItem('token'),
            comment_id: item.say_id
          })
            .then(res=>{
              if(res.data.code == 1){
                this.getMy(1)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this. reduceComment(item,index)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
          })
        }else if(item.flag == 4||item.flag == 7) {
          this.$http.post(this.api + '/home/Comment/del_leavemsg', {
            token: localStorage.getItem('token'),
            msg_id: item.say_id
          })
            .then(res => {
              if (res.data.code == 1) {
                this.getMy(1)
              } else if (res.data.code == 3) {
                this.$http.post(this.api + '/home/index/token')
                  .then(res => {
                    localStorage.setItem('token', res.data.data)
                    this.reduceComment(item, index)
                  })
              } else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      }
    },
    mounted() {
		  this.getToMe(1)
		  if(localStorage.getItem('userImg')){
		    this.imgUrl = localStorage.getItem('userImg')
      }else{
        this.imgUrl = this.commenImg
      }
    }
  }



